import { NavLink } from "react-router-dom";
import { useAuth } from "../../Context/AuthProvider";
import { useCart } from "../../Context/CartProvider";
import "./Navigation.css";
const Navigation = () => {
  const { cart } = useCart();
  const userAuth = useAuth();
  console.log(userAuth);
  return (
    <header className="MainNavigation">
      <nav>
        <ul>
          <svg
            width="24"
            height="24"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M24 7.8 6.442 15.276c-1.456.616-2.679.925-3.668.925-1.12 0-1.933-.392-2.437-1.177-.317-.504-.41-1.143-.28-1.918.13-.775.476-1.6 1.036-2.478.467-.71 1.232-1.643 2.297-2.8a6.122 6.122 0 0 0-.784 1.848c-.28 1.195-.028 2.072.756 2.632.373.261.886.392 1.54.392.522 0 1.11-.084 1.764-.252L24 7.8Z"
              fill="#581C87"
            />
          </svg>
          <li>
            <NavLink to="/" activeClassName="activeLink" exact>
              Home
            </NavLink>
          </li>
        </ul>

        <ul>
          <li className="cartLink">
            <NavLink to="/cart" activeClassName="activeLink" exact>
              Cart
            </NavLink>
            <span>{cart.length}</span>
          </li>
          <li className="LoginLink">
            <NavLink
              to={userAuth ? "/profile" : "/login"}
              activeClassName="activeLink"
              exact
            >
              {userAuth ? userAuth.name : "Login"}
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Navigation;
